<template>
  <table class="table space-y-6 border-separate table-auto dark:text-white">
    <thead class="my-2 bg-gray-300 rounded dark:bg-gray-900">
      <tr>
        <th class="p-3">Subject &amp; Chapter</th>

        <th class="p-3">Starts</th>
        <th class="p-3">Ends</th>
        <th class="p-3">Details</th>
      </tr>
    </thead>
    <tbody>
      <tr
        v-for="(e, i) in challenges"
        :key="i"
        class="bg-gray-200 rounded-md dark:bg-gray-800"
      >
        <td class="p-3">
          <p class="font-semibold">{{ e.chapter }}</p>
          {{ e.subject }}
        </td>
        <td class="p-3">
          {{ formattedDate(e.start) }}
        </td>

        <td class="p-3">
          {{ formattedDate(e.end) }}
        </td>
        <td class="p-3">
          <button
            class="p-1 bg-gray-900 rounded-full shadow-lg"
            @click="$emit('selectedExam', i)"
          >
            <img
              src="https://img.icons8.com/plasticine/100/000000/view.png"
              class="w-10"
            />
          </button>
        </td>
      </tr>
    </tbody>
  </table>
</template>

<script>
import { useDateFormat } from "@/hooks/dateFormat";
export default {
  emits: ["selectedExam"],
  props: {
    challenges: {
      type: Array,
      required: true,
    },
  },
  setup() {
    const { formattedDate, formattedDateC } = useDateFormat();
    return {
      formattedDate,
      formattedDateC,
    };
  },
};
</script>

