<template>
  <div class="min-h-screen dark:bg-gray-600 bangla">
    <navbar
      v-if="$route.name !== 'Login' && $route.name !== 'routine'"
    ></navbar>
    <router-view v-slot="{ Component }">
      <transition name="route" mode="out-in">
        <component :is="Component"></component>
      </transition>
    </router-view>
  </div>
</template>

<script>
import Navbar from "@/components/Navbar";
import { mapGetters } from "vuex";
export default {
  components: {
    Navbar,
  },
  beforeMount() {
    this.$store.dispatch("initTheme");
  },
  computed: {
    ...mapGetters({ theme: "getTheme" }),
  },
  watch: {
    theme(newTheme) {
      newTheme === "light"
        ? document.querySelector("html").classList.remove("dark")
        : document.querySelector("html").classList.add("dark");
    },
  },
  methods: {
    navigateTocurrentPath() {
      let currentPath = localStorage.getItem("path");
      if (currentPath) {
        localStorage.removeItem("path");
        this.$router.replace(currentPath);
      }
    },
  },

  created() {
    this.navigateTocurrentPath();
    let user = localStorage.getItem("s22ssrb");
    let routine = localStorage.getItem("routines22");
    if (user != undefined) {
      this.$store.commit("SET_USER", JSON.parse(user));
      if (routine) {
        this.$store.commit("SET_ROUTINE", JSON.parse(routine));
      } else {
        console.log("hello");
        this.$store.dispatch("authenticate", { userid: user.ttrx });
      }
      this.$store.commit("toggleAuth");
    }
  },
};
</script>

<style lang="scss" scoped>
/* route transitions */
.route {
  &-enter-from {
    transform: translateX(100px);
    opacity: 0;
  }
  &-enter-active {
    transition: all 0.5s ease-out;
  }
  &-leave-to {
    transform: translateX(-100px);
    opacity: 0;
  }
  &-leave-active {
    transition: all 0.5s ease-in;
  }
}
</style>