<template>
  <nav class="border-b-2 border-gray-300 lato dark:bg-gray-800">
    <div class="container flex items-center justify-between h-20 md:mx-auto">
      <div class="mx-2">
        <router-link to="/" class="relative flex items-center">
          <img
            src="https://img.icons8.com/external-tulpahn-flat-tulpahn/64/000000/external-education-ecology-tulpahn-flat-tulpahn.png"
            class="h-10 mr-2"
          />
          <h2 class="text-xl font-bold text-gray-900 poppins dark:text-white">
            HSC22 Short Syllabus <br />
            Special Revision Plan
          </h2>
        </router-link>
      </div>
      <div class="justify-end hidden lg:flex">
        <div class="items-center justify-center flex-1 hidden ml-10 md:flex">
          <router-link
            v-for="(m, i) in menuItems"
            :to="m.link"
            :key="i"
            exact-active-class="text-white bg-gray-700 shadow-xl dark:bg--indigo-400 hover:bg-gray-600 dark:bg-gray-900"
            class="flex items-center px-3 py-2 mr-2 font-semibold rounded-md shadow-sm  bg-gray-50 dark:bg-gray-600 hover:bg-gray-200 dark:text-white dark:hover:bg-gray-900 hover:shadow-lg"
          >
            <!-- <i class="mr-1 fas" :class="m.icon"></i> -->
            <img :src="m.icon" class="w-8 h-8 mr-2" />
            {{ m.text }}</router-link
          >
          <div class="flex justify-end gap-3">
            <button
              v-if="isLoggedIn"
              class="px-3 font-semibold bg-gray-300 border border-red-400 rounded-full shadow  dark:bg-black dark:text-white"
            >
              <!-- Roll: {{ user.roll }} -->
            </button>
            <button @click="toggleTheme" class="rounded-full shadow-lg">
              <img
                src="https://img.icons8.com/bubbles/50/000000/partly-cloudy-night.png"
                v-if="theme == 'light'"
              />
              <img
                src="https://img.icons8.com/color/48/000000/sun--v2.png"
                v-else
              />
            </button>
          </div>
        </div>
      </div>
      <div class="lg:hidden">
        <button
          class="px-2 py-1 mx-2 bg-gray-100 rounded outline-none  hover:bg-gray-300"
          @click="mobileMenu = !mobileMenu"
        >
          <span class="material-icons">
            {{ mobileMenu ? "close" : "menu" }}
          </span>
        </button>
      </div>
    </div>
    <div
      class="container p-2 mx-auto mb-2 rounded  bg-gray-50 animate__fadeIn animate__animated"
      v-if="mobileMenu"
    >
      <router-link
        v-for="(m, i) in menuItems"
        :to="m.link"
        :key="i"
        @click="mobileMenu = !mobileMenu"
        class="flex items-center px-5 py-1 my-2 font-semibold rounded-md  hover:bg-gray-200"
        exact-active-class="text-white bg-gray-800 shadow-xl hover:bg-gray-600 dark:bg-gray-900"
      >
        <img :src="m.icon" class="w-8 h-8 mr-2" />{{ m.text }}</router-link
      >
    </div>
    <div
      class="flex justify-center gap-3 my-3 animate__fadeIn animate__animated"
      v-if="mobileMenu"
    >
      <button
        v-if="isLoggedIn"
        class="px-3 font-semibold bg-gray-300 rounded-full shadow  dark:bg-black dark:text-white"
      >
        <!-- Roll: {{ user.roll }} -->
      </button>
      <button @click="toggleTheme">
        <img
          src="https://img.icons8.com/bubbles/50/000000/partly-cloudy-night.png"
          v-if="theme == 'light'"
        />
        <img src="https://img.icons8.com/color/48/000000/sun--v2.png" v-else />
      </button>
    </div>
  </nav>
</template>

<script>
import { mapGetters, mapState } from "vuex";
export default {
  data() {
    return {
      mobileMenu: false,
      menuItems: [
        {
          text: "Routine",
          icon: "https://img.icons8.com/external-flatart-icons-flat-flatarticons/64/000000/external-study-high-school-flatart-icons-flat-flatarticons-1.png",
          link: "/",
        },
        {
          text: "Profile",
          icon: "https://img.icons8.com/color/96/000000/user.png",
          link: "/profile",
        },
        {
          text: "QnA",
          icon: "https://img.icons8.com/office/50/000000/faq.png",
          link: "/qna",
        },
      ],
    };
  },
  methods: {
    toggleTheme() {
      this.$store.commit("SET_THEME", this.theme == "light" ? "dark" : "light");
    },
  },
  computed: {
    ...mapGetters({ theme: "getTheme" }),
    ...mapState({
      isLoggedIn: "userLoggedIn",
      user: "user",
    }),
  },
};
</script>

<style lang="scss" scoped>
@import url("https://fonts.googleapis.com/css2?family=Lato:wght@400&display=swap");
.lato {
  font-family: "Lato", sans-serif;
}
</style>