<template>
  <div class="pb-10">
    <div v-if="upcoming">
      <current-challenge :upcoming="upcoming">
        <selected-chapter :selected="upcoming" />
      </current-challenge>
    </div>
    <div class="animate_animated animate__bounceIn" v-if="routines.length > 0">
      <div class="gap-3 my-4 subject__filters">
        <button
          :class="`inline-block shadow-lg rounded-md bg-primary hover:bg-success text-white px-3 py-2 ${
            se == '' && 'bg-success'
          }`"
          @click="selectSubject('')"
        >
          All
        </button>
        <template v-for="(s, i) in subjects" :key="i">
          <button
            :class="`inline-block shadow-lg rounded-md bg-primary hover:bg-success text-white px-3 py-2 ${
              se == s && 'bg-success'
            }`"
            @click="selectSubject(s)"
          >
            {{ s }}
          </button>
        </template>
      </div>
    </div>
    <div v-if="loading">
      <h1 class="mx-auto text-center">Please Wait...</h1>
    </div>
    <div class="container hidden mx-auto md:block" v-if="routines.length > 0">
      <pc-routine :challenges="routines" @selectedExam="selectedExam" />
    </div>
    <div class="container mx-auto md:hidden" v-if="routines.length > 0">
      <mobile-routine :exams="routines" @selectedExam="selectedExam" />
    </div>
    <!-- <div v-if="routines.length > 0" class="my-4 text-center">
      <router-link
        to="/print"
        class="inline-block px-6 py-4 text-white bg-red-500 rounded"
        >Print</router-link
      >
    </div> -->
    <modal v-if="selected">
      <div class="text-center" v-if="selected">
        <selected-chapter
          :selected="selected"
          examtype="routine"
          :liveClass="false"
        />
      </div>
    </modal>
  </div>
</template>

<script>
import _ from "lodash";
import { mapMutations, mapState, mapActions } from "vuex";
import { routine } from "../plugins/auth";
import PcRoutine from "@/components/PcRoutine.vue";
import CurrentChallenge from "../components/CurrentChallenge.vue";
import SelectedChapter from "../components/SelectedChapter.vue";
import MobileRoutine from "../components/MobileRoutine.vue";
import Modal from "../components/Modal.vue";
export default {
  components: {
    PcRoutine,
    CurrentChallenge,
    SelectedChapter,
    MobileRoutine,
    Modal,
  },
  data() {
    return {
      loading: false,
      all: [],
      routines: [],
      selectedChapters: [],
      subjects: new Set(),
      selected: null,
      se: "",
      upcoming: [],
    };
  },
  methods: {
    ...mapMutations(["toggleModal"]),
    ...mapActions(["authenticate"]),
    async prepareRoutine() {
      let res = await fetch(routine);
      let { routines: shortSyllabus } = await res.json();
      shortSyllabus = shortSyllabus.filter((r) => r.ss22);
      if (shortSyllabus.length > 0) {
        let routines = _.values(
          _.merge(
            _.keyBy(JSON.parse(localStorage.getItem("routines22")), "id"),
            _.keyBy(shortSyllabus, "id")
          )
        );
        this.all = routines.filter((s) => s.complete !== undefined);
        this.routines = this.all.map((r) => {
          if (r.subject) {
            let s = r.subject.split(" ")[0];
            if (s == "Higher") {
              this.subjects.add("Higher Math");
            } else {
              this.subjects.add(s);
            }
          }
          return {
            ...r,
          };
        });

        let upcommings = this.routines.filter((k) => {
          return k.end > Date.now();
        });
        this.upcoming = upcommings[0];
      }
      this.loading = false;
    },
    async getUserRoutine() {
      this.loading = true;
      let user = JSON.parse(localStorage.getItem("s22ssrb"));
      let routine = localStorage.getItem("routines22");
      if (!routine) {
        await this.authenticate({ userid: user.ttrx });
        this.prepareRoutine();
      } else {
        this.prepareRoutine();
      }
    },
    selectedExam(i) {
      this.selected = this.routines[i];
      this.toggleModal();
    },
    selectSubject(s) {
      this.se = s;
      this.routines = this.all.filter((r) => {
        if (r.subject) {
          if (r.subject.toLowerCase().includes(s.toLowerCase())) {
            return r;
          }
        }
      });
    },
  },
  created() {
    this.getUserRoutine();
  },
  computed: {
    ...mapState({
      userroutine: (state) => state.routine,
    }),
  },
};
</script>

