import { createStore } from "vuex";
import { auth } from "../plugins/auth";
export default createStore({
  state: {
    routine: null,
    options: {},
    start: 0,
    user: null,
    theme: "light",
    userLoggedIn: false,
    showModal: false,
  },
  mutations: {
    toggleAuth(state) {
      state.userLoggedIn = !state.userLoggedIn;
    },
    toggleModal(state) {
      state.showModal = !state.showModal;
    },
    SET_OPTIONS(state, payload) {
      state.options = payload;
    },
    SET_ROUTINE(state, payload) {
      state.routine = payload;
    },
    SET_START(state, payload) {
      state.start = payload;
    },

    SET_USER(state, payload) {
      state.user = payload;
    },
    SET_THEME(state, theme) {
      state.theme = theme;
      localStorage.theme = theme;
    },
  },
  actions: {
    setOptions({ commit }, payload) {
      commit("SET_OPTIONS", payload.options);
    },
    async authenticate({ commit }, payload) {
      let res = await fetch(`${auth}?ttrx=${payload.userid}`);
      let data = await res.json();

      if (data && data.auth) {
        let { name, routine } = data;
        if (name) {
          if (routine) {
            commit("SET_ROUTINE", routine);
            localStorage.setItem("routines22", JSON.stringify(routine));
            commit("SET_START", routine.start);
            delete data.routine;
          }
          commit("SET_USER", data);
          localStorage.setItem("s22ssrb", JSON.stringify(data));
          if (!routine) {
            payload.router.push(`/routine`);
          } else {
            if (payload.route.name === "Login") {
              payload.router.replace({ name: "home" });
            }
          }
        } else {
          payload.router.push(`/register?ttrx=${payload.userid}`);
        }
      } else if (data && !data.auth) {
        localStorage.removeItem("s22ssrb");
        localStorage.removeItem("routines22");
        payload.router.push({
          name: "restricted",
        });
      }
    },
    async register({ commit }, payload) {
      try {
        let res = await fetch(auth, {
          method: "POST",
          body: payload.body,
        });

        let data = await res.json();
        commit("SET_USER", data);
        localStorage.setItem("s22ssrb", JSON.stringify(data));
        payload.router.replace({ name: "routine" });
      } catch (error) {
        console.log(error);
      }
    },
    initTheme({ commit }) {
      const cachedTheme = localStorage.theme ? localStorage.theme : false;
      //  `true` if the user has set theme to `dark` on browser/OS
      const userPrefersDark = window.matchMedia("(prefers-color-scheme: dark)")
        .matches;

      if (cachedTheme) commit("SET_THEME", cachedTheme);
      else if (userPrefersDark) commit("SET_THEME", "dark");
      else commit("SET_THEME", "light");
    },
  },
  getters: {
    getTheme: (state) => {
      return state.theme;
    },
  },
  modules: {},
});
