<template>
  <div class="dark:text-white">
    <h2 class="py-3 text-2xl font-semibold ma-auto">
      {{ selected.chapter }}
    </h2>
    <h2 class="py-3 text-xl font-semibold -mt-7 ma-auto">
      {{ selected.subject }}
    </h2>
    <div class="mt-3 text-lg">
      <h2 class="text-2xl font-semibold text-primary dark:text-green-400">
        Challenge Duration
      </h2>
      <h3 class="mt-2">
        {{ formattedDate(selected.start) }} <br />
        to <br />
        {{ formattedDate(selected.end) }}
      </h3>

      <div class="flex flex-wrap justify-center gap-3">
        <a
          class="inline-block my-2 danger_btn"
          :href="selected.playlist"
          target="_blank"
          v-if="selected.playlist"
        >
          Playlist</a
        >
        <button
          class="inline-block my-2 danger_btn"
          v-else
          @click="upcomingalert"
        >
          Playlist
        </button>
        <a
          class="inline-block my-2 danger_btn"
          :href="selected.lecturesheet"
          target="_blank"
          v-if="selected.lecturesheet"
          >Lecture Sheet</a
        >
        <button
          class="inline-block my-2 danger_btn"
          v-else
          @click="upcomingalert"
        >
          Lecture Sheet
        </button>
        <a
          class="inline-block my-2 danger_btn"
          :href="selected.modelcq"
          target="_blank"
          v-if="selected.modelcq"
          >Model CQ</a
        >
        <button
          class="inline-block my-2 danger_btn"
          v-else
          @click="upcomingalert"
        >
          Model CQ
        </button>
      </div>
      <div class="pb-2 mx-auto space-x-2 space-y-2">
        <router-link
          class="inline-block my-2 danger_btn"
          v-if="new Date(selected.start).getTime() < Date.now()"
          :to="`/exam/${selected.id}?e=${selected.start}&w=${
            selected.mcqsheetlink
          }&d=${selected.complete ? 'm' : 'n'}`"
          >Start Exam
        </router-link>
        <button
          class="inline-block my-2 danger_btn"
          v-else
          @click="upcomingalert(selected.start)"
        >
          Start Exam
        </button>
        <router-link
          v-if="selected.start < Date.now()"
          class="inline-block mx-2 my-2 danger_btn"
          :to="`/ranking/${selected.id}`"
          >Leaderboard</router-link
        >
        <button
          class="inline-block my-2 danger_btn"
          v-else
          @click="upcomingalert(selected.start)"
        >
          Leaderboard
        </button>
        <router-link
          class="inline-block my-2 danger_btn"
          v-if="selected.end < Date.now()"
          :to="`/solution/${selected.id}?e=${selected.start}&w=${
            selected.mcqsheetlink
          }&d=${selected.complete ? 'm' : 'n'}`"
          >Solution
        </router-link>
        <button
          class="inline-block my-2 danger_btn"
          v-else
          @click="endalert(selected.end)"
        >
          Solution
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { useDateFormat } from "@/hooks/dateFormat";
export default {
  name: "SelectedChapterInfo",
  props: {
    selected: { type: Object, required: true },
  },
  setup() {
    const { formattedDate, formattedDateC } = useDateFormat();
    return {
      formattedDate,
      formattedDateC,
    };
  },
  methods: {
    upcomingalert(extime = "") {
      this.$swal({
        icon: "warning",
        title: "Upcomming",
        text: `Wait till ${
          extime ? this.formattedDate(extime).toString() : ""
        }`,
      });
    },
    endalert(extime = "") {
      this.$swal({
        icon: "warning",
        title: "Wait",
        text: `Will be available after ${
          extime ? this.formattedDate(extime).toString() : ""
        }`,
      });
    },
  },
};
</script>

<style lang="scss" scoped>
</style>