import { createRouter, createWebHistory } from "vue-router";
import Home from "../views/Home.vue";

const routes = [
  {
    path: "/",
    name: "home",
    component: Home,
    meta: { requiresAuth: true },
  },
  {
    path: "/login",
    name: "Login",
    component: () => import("@/views/auth/Login"),
  },
  {
    path: "/register",
    name: "Register",
    component: () => import("@/views/auth/Register"),
  },
  {
    path: "/restricted",
    name: "restricted",
    component: () => import("@/views/auth/accessrestricted"),
  },
  {
    path: "/routine",
    name: "routine",
    component: () => import("@/views/Routine.vue"),
    meta: { requiresAuth: true },
  },
  {
    path: "/print",
    name: "Print",
    component: () => import("@/views/Print.vue"),
  },
  {
    path: "/profile",
    name: "Profile",
    component: () => import("@/views/Profile.vue"),
    meta: { requiresAuth: true },
  },
  {
    path: "/qna",
    name: "QNA",
    component: () => import("@/views/QnA.vue"),
  },
  {
    path: "/exam/:id",
    name: "ExamQuestion",
    meta: { requiresAuth: true },
    component: () => import("../views/exams/MCQuestion.vue"),
  },
  {
    path: "/solution/:id",
    name: "ExamSolution",
    meta: { requiresAuth: true },
    component: () => import("../views/exams/Solution.vue"),
  },
  {
    path: "/ranking/:id",
    name: "Ranking",
    meta: { requiresAuth: true },
    component: () => import("../views/exams/MCQLeaderboard.vue"),
  },
  {
    path: "/play",
    name: "Play",
    component: () => import("@/views/About"),
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

router.beforeEach((to, from, next) => {
  if (!to.matched.some((record) => record.meta.requiresAuth)) {
    next();
    return;
  }

  if (localStorage.getItem("s22ssrb")) {
    next();
    return;
  }

  next({ name: "restricted" });
});
export default router;
