<template>
  <!-- Auth Modal -->
  <div
    class="fixed inset-0 z-10 overflow-y-auto"
    :class="{ hidden: !showModal }"
    id="modal"
  >
    <div
      class="flex items-end justify-center min-h-screen px-4 pt-4 pb-20 text-center  sm:block sm:p-0"
    >
      <div class="fixed inset-0 transition-opacity">
        <div class="absolute inset-0 bg-gray-800 opacity-75"></div>
      </div>

      <!-- This element is to trick the browser into centering the modal contents. -->
      <span class="hidden sm:inline-block sm:align-middle sm:h-screen"
        >&#8203;</span
      >

      <div
        class="inline-block overflow-hidden text-left align-bottom transition-all transform rounded-lg shadow-xl  bg-secondary dark:bg-gray-700 sm:my-8 sm:align-middle sm:max-w-lg sm:w-full"
      >
        <!-- Add margin if you want to see some of the overlay behind the modal-->
        <div class="px-6 pt-2">
          <!--Title-->
          <div class="flex justify-end">
            <button
              class="z-50 flex items-center justify-center w-8 h-8 p-2 text-white transition duration-150 transform bg-red-500 rounded-full cursor-pointer  modal-close hover:scale-105"
              @click.prevent="toggleModal"
            >
              <span class="material-icons">close</span>
            </button>
          </div>

          <slot />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapMutations, mapState } from "vuex";
export default {
  methods: {
    ...mapMutations(["toggleModal"]),
  },
  computed: {
    ...mapState(["showModal"]),
  },
};
</script>

<style lang="scss" scoped>
</style>
