<template>
  <div
    v-for="(e, i) in exams"
    :key="i"
    class="p-5 mx-2 my-4 space-y-2 rounded bg-gray-50"
  >
    <div class="flex justify-between">
      <p>Subject:</p>
      <p>{{ e.subject }}</p>
    </div>
    <hr />

    <div class="flex justify-between">
      <p>Chapter:</p>
      <p>{{ e.chapter }}</p>
    </div>
    <hr />
    <div class="flex justify-between">
      <p>Starting:</p>
      <p>{{ formattedDate(e.start) }}</p>
    </div>

    <hr />

    <div class="flex justify-between">
      <p>Ending:</p>
      <p>{{ formattedDate(e.end) }}</p>
    </div>

    <hr />
    <div class="flex items-center justify-between">
      Details:
      <button
        class="p-1 bg-gray-900 rounded-full"
        @click="$emit('selectedExam', i)"
      >
        <img
          src="https://img.icons8.com/plasticine/100/000000/view.png"
          class="w-8"
        />
      </button>
    </div>
  </div>
</template>

<script>
import { useDateFormat } from "@/hooks/dateFormat";
export default {
  emits: ["selectedExam"],
  props: {
    exams: {
      type: Array,
    },
    subject: {
      default: "",
    },
  },
  setup() {
    const { formattedDate, formattedDateC } = useDateFormat();
    return {
      formattedDate,
      formattedDateC,
    };
  },
};
</script>

